
  @import url('https://fonts.googleapis.com/css2?family=Cairo&family=Lato:wght@300&family=League+Spartan:wght@200;300;400;500;600;700&family=Open+Sans:wght@400;700&family=Roboto:wght@100;300;400;500&family=Work+Sans:wght@200;300;400;500;600;700;800&display=swap');

:root{
  --primary: #00A97F;
  --light:rgba(133,198,218,255);
  --dark:rgba(13,13,10,255);
  --darker:rgba(234,157,90,255);
  --lighter:#cbd6cf;
  --lighter1:#c3dacc86;
  --lighter2:#e4f0e9da;
  --darken: #cca27e;
  --color: orangered;
  --search: #FF6000;
  --primary1: #2b3d4c;
  --primary2: #354d61;
}

.logo {
  max-width: 100%;
  height: 40px;

}


a {
  text-decoration: none;
  font-size: 14px;
}
p {
  font-size: 16px;
  font-weight: 400px;
  
}
h1 {
  font-weight: 600;
  color: var(--search);
  margin: 0 0 0 40px;
  font-size: 32px;
}
h2{
  font-weight: 400;
  margin: 0 0 0 40px;
  color: var(--primary1);
font-size: 32px;
}

button {
  padding: 12px 10px !important;
  font-size: 14px;
  background-color: var(--primary1);
  color: white;
}
.navbar {
  background-color: var(--lighter) !important;
}

nav .navbar-nav a{
  color: var(--primary2);
  margin-right: 30px;
  font-weight: 400px;
  letter-spacing: 1px;
  font-size: 17px;
transition: .3s;
}
nav .navbar-nav a:hover{
color: var(--primary);
text-decoration: underline;
}

nav .nav-links-nav a {
  color: var(--darken);
  font-size: 20px !important;
}

nav .nav-links-nav a:hover {
  color: var(--darker);

}

[type=submit]:not(:disabled), button:not(:disabled) {
  cursor: pointer;
  background-color: var(--lighter2);
  color: var(--primary1);
  transition: .3s;
  border: 1px solid var(--primary1);
  border-radius: 12px;
}
[type=submit]:not(:disabled):hover, button:not(:disabled) {
  background-color: var(--lighter);
}
.navbar-toggler-icon {
  background-color: var(--darken);
  border: 2px solid var(--search);
}
 /* start footer */

footer {
  background-color: var(--lighter1);
}

.footer-info img {
    padding: 10px 8px !important;
    margin-right: 5px;
    border-radius: 12px;
    border: 1px solid var(--primary);
    transition: .5s ease;
}
.footer .social-icon a {
  color: var(--darken);
}
.footer .social-icon a:hover {
  color: var(--darker);
}
.footer-info img:hover {
  transform: translateY(-10px);
}
.footer-info a {
  color: var(--primary1);
  font-weight: 800px;
}
.footer .call {
  color: var(--darken);
  font-size: 17px;
}
.footer .call:hover {
  color: var(--primary1);
}
/* end */
.banner {
  margin-top: 15px;
}

.banner-info {
  background: url(./assets/images/back.jpg);
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 60vh;
}
.banner-info p {
  margin: 0 0 0 40px;
}
.button-link {
  padding: 10px 12px;
  font-size: 18px;
  background: var(--primary1);
  color: white;
  border-radius: 10px;
  border: 1px solid transparent;
  transition: .3s ease;
  margin: 40px;
}
.button-link:hover {
  background: white;
  color: var(--primary1);
  border: 1px solid var(--primary1);
}
/* start hero */
.hero .hero-details img{
  width: 50px;
}

.fee {
  color: var(--primary1);
}
.feez {
  margin-top: 10px;
  margin-left: 35px;
}
/* start featured */

.featured-products .card {
  border-radius: 20px;
  transition: 0.5s ease;
  max-width: 250px;
  padding: 12px 10px;
  overflow: hidden;
  max-height: 465px;
}
.featured-products .card img {
  border-radius: 20px;
  max-width: 100%;
  max-height: 200px;
}
.featured-products .card-body {
  top: 30%;
  right: -100px;
  transition: 0.8s ease;
}
.featured-products .card:hover .card-body{
  right: 80px;
}

.featured-products .card:hover {
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.08);
}

.featured-products .card-text{
  color: var(--darken);
  font-size: 14px;
  text-transform: lowercase;
}
.featured-products h5{
  font-weight: 500;
  color: var(--primary1);
  font-size: 18px;
}
.featured-products .card h5:hover{
  color: var(--primary);
}

.featured-products .price strike{
  color: var(--darker);
}
/* //.repair-details */

.repair-details{
  background: url(/src/assets/images/banner/b2.jpg);
  background-size: cover;
  width: 100%;
  height: 30vh;
  justify-content: center;
  background-position: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
}

.repair-details button{
  color: white;
  border-radius: 15px;
  background-color: var(--primary);
  border: 1px solid transparent;
  transition: 0.5s ease;
}
.repair-details button:hover{
  border: 1px solid var(--search);
  background-color: transparent;
}

.new-arrivals .card {
  border-radius: 20px;
  transition: 0.5s ease;
  max-width: 250px;
  padding: 12px 10px;
  overflow: hidden;
  max-height: 465px;
}
.new-arrivals .card img {
  border-radius: 20px;
  max-width: 100%;
  max-height: 200px;
}
.new-arrivals .card-body {
  top: 30%;
  right: -100px;
  transition: 0.8s ease;
}
.new-arrivals .card:hover .card-body{
  right: 80px;
}

.new-arrivals .card:hover {
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.08);
}
.new-arrivals .card h5:hover{
  color: var(--primary1);
}
.new-arrivals .card-text{
  color: var(--darken);
  font-size: 14px;
  text-transform: lowercase;
}
.new-arrivals h5{
  font-weight: 500;
  color: var(--primary1);
  font-size: 18px;
}
.new-arrivals .price strike{
  color: var(--darker);
}

/* start hot */
.hot-deals .card{
  padding: 12px 10px;
}

.hot-deals .card img{
  min-height: 150px;
  justify-content: center;
}

.hot-deals button{
  color: white;
  background-color: var(--darken);
  transition: 0.5s;
  border:  1px solid transparent;
  border-radius: 15px;
  font-weight: bold;
}

.hot-deals button:hover{
  background: transparent;
  color: var(--search);
  border: 1px solid var(--darker);
}
.blogs button{
  color: white;
  
  transition: 0.5s;
  border:  1px solid transparent;
  border-radius: 15px;
  font-weight: bold;
}

.blogs button:hover{
  background: transparent;
  color: var(--search);
  border: 1px solid var(--darker);
}

/* start news */
.news-letter{
  background-color: var(--lighter);
  margin-bottom: 20px;
}
#basic-addon2{
  background-color: transparent;
  text-align: center;
  line-height: 0;
  transition: 0.5s ease;
  font-size: 16px;
}

#basic-addon2:hover{
  color: var(--search);
}

#news-input{
  height: 3rem;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

/* blog starts */
.blog-wrapper{
  background: url(/src/assets/images/banner/b19.jpg);
  background-size: cover;
  width: 100%;
  height: 40vh;
  justify-content: center;
  background-position: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
}

/* start shop */
.shop-banner{
  background: url(/src/assets/images/banner/b1.jpg);
  background-size: cover;
  width: 100%;
  height: 30vh;
  justify-content: center;
  background-position: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
}

.shop-banner .shop-details .title-green{
  color: var(--primary);
}

/* start shop-details */
.shop-products .card{
  border-radius: 30px;
  transition: 0.5s ease;
  max-width: 250px;
  padding: 12px 10px;
  overflow: hidden;
  max-height: 520px;
}
.shop-products .card img {
  border-radius: 10px;
  max-width: 100%;
  max-height: 200px;
}
.shop-products .card-body {
  top: 30%;
  right: -100px;
  transition: 0.8s ease;
}
.shop-products .card:hover .card-body{
  right: 80px;
}
.shop-products .card:hover {
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.08);
}
.shop-products h5{
  font-weight: 500;
  color: var(--primary1);
  font-size: 18px;
}
.shop-products .card h5:hover{
  color: var(--primary);
}
.shop-products p{
  font-weight: 500;
  color: var(--primary);
  font-size: 12px;
}
.shop-products .view{
  font-weight: 500;
  color: var(--primary1);
  font-size: 12px;
  border: 1px solid var(--primary);
  background-color: transparent;
}
.shop-products .myButton{
  font-weight: 500;
  color: white;
  font-size: 12px;
  border: 1px solid var(--primary);
  background-color: var(--primary1);
}
/* start cart */
.cart-item-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.cart-item-image {
  flex: 1 1 100%;
  max-width: 150px;
  margin-right: 20px;
}

.cart-item-image img {
  width: 100%;
  height: auto;
}

.cart-item-details {
  flex: 2 1 100%;
  margin-right: 20px;
}

.cart-item-details h2 {
  margin: 0;
  font-size: 1.2rem;
}

.cart-item-brand {
  margin: 0;
  font-size: 0.9rem;
  color: #666;
}

.cart-item-id {
  margin: 0;
  font-size: 0.8rem;
  color: #999;
}

.cart-item-count {
  flex: 1 1 100%;
  margin-right: 20px;
}

.count-handler {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 150px;
  margin: 0 auto;
}

.count-handler button {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  color: #333;
  font-size: 1rem;
  padding: 5px 10px;
}

.count-handler input {
  width: 50px;
  text-align: center;
  border: none;
  border-bottom: 1px solid #ccc;
  font-size: 1rem;
  margin: 0 10px;
}

.cart-item-delete {
  flex: 1 1 100%;
  text-align: center;
}

.cart-item-delete button {
  background-color: #fff;
  border: none;
  color: #999;
  font-size: 1.5rem;
  padding: 0;
  cursor: pointer;
}

@media (max-width: 768px) {
  .cart-item-container {
    flex-wrap: nowrap;
  }

  .cart-item-image {
    max-width: 100px;
    margin-right: 10px;
  }

  .cart-item-details {
    margin-right: 10px;
  }

  .count-handler {
    max-width: 100px;
  }
  .banner h1, p{
    color: black;
  }
p{
  color: var(--primary1);
}

.carousel-inner .text{
  color: white;
}

.carousel-inner h1{
  color: var(--search);
}
}
/* about start */
.about-wrapper{
  background: url(/src/assets/images/about/banner.png);
  background-size: cover;
  width: 100%;
  height: 40vh;
  justify-content: center;
  background-position: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
}

.abouts img{
  width: 28rem;
}

.about-app video{
  width: 100%;
  height: 100%;
  border-radius: 20px;
}

.slide img{
  width: 5rem;
  margin: 0 20px;
}
/* contact start */
.map {
  height: 450px;
  align-items: center;
  max-width: 400px;
  border-radius: 15px;
}
.map iframe{
  height: 450px;
  align-items: center;
  max-width: 400px;
}

.contact-wrapper{
  background: url(/src/assets/images/back.jpg);
  background-size: cover;
  width: 100%;
  height: 35vh;
  justify-content: center;
  background-position: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
}
.contact-wrapper .text{
  color: var(--search) !important;
}
.contact-wrapper-details .card{
  border: none;
  background-color: var(--lighter2);
}

.contact-wrapper .text{
  color: var(--search);
}
.contact-message .card{
  background: var(--lighter2);
  border: none;
  border-radius: 25px;
}

.contact-message label{
  font-size: 18px;
}

.contact-message input{
  font-size: 14px;
  height: 40px;
}
/* login start */
.text-center{
  color: var(--primary);
}

.login-wrapper{
  background-color: var(--lighter1);
}

.login-wrapper .card{
  border: 1px solid var(--lighter);
  border-radius: 10px;
max-height: 590px;

}

.login-wrapper label{
  font-size: 18px;
}

.login-wrapper input{
  font-size: 14px;
  height: 40px;
}

.login-wrapper .form-link{
  font-size: 14px;
  transition: 0.5s ease;
}

.login-wrapper .form-link:hover{
  color: var(--search);
}

.login-wrapper button{
  font-size: 18px;
  padding: 12px 24px;
  background-color: var(--primary1);
  color: white;
  border: 1px solid transparent;
  transition: 0.5s ease;
}

.login-wrapper button:hover{
  background-color: transparent;
  border: 1px solid var(--primary1);
  color: var(--primary1);
}
/* check start */
.checkout{
  background-color: var(--lighter2);
}

.checkout-details label{
  font-size: 18px;
}

.checkout-details input{
  font-size: 14px;
  height: 50px;
}

.checkout img{
  width: 8rem;
}

.checkout .cart-2{
  background-color: white;
}

.checkout .card{
  border-radius: 10px;
}